”政策研究课题组，组成了惠民政策研究的专家智库。")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font",staticStyle:{"font-weight":"bold","width":"100%","text-indent":"0rem"}},[_vm._v("“国普惠民”政策研究课题组发起成员：")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font",staticStyle:{"text-align":"left","line-height":"0.3rem","width":"100%"}},[_vm._v(" 国家发改委国合中心双创办主任 —邓智华博士")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font",staticStyle:{"text-align":"left","line-height":"0.3rem","width":"100%"}},[_vm._v(" 中国老龄事业发展基金会安享工程专项基金管委会秘书长—邓少军")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font",staticStyle:{"text-align":"left","line-height":"0.3rem","width":"100%"}},[_vm._v(" 中国民族卫生协会医院创新专业管理委员会会长—李易博")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font",staticStyle:{"text-align":"left","line-height":"0.3rem","width":"100%"}},[_vm._v(" 中国消费者保护权益基金会打假工作委员会品牌部副主任—李庆忠")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font",staticStyle:{"text-align":"left","line-height":"0.3rem","width":"100%"}},[_vm._v(" 中华老干部志工委副秘书长—凤兆俊")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font",staticStyle:{"text-align":"left","line-height":"0.3rem","width":"100%"}},[_vm._v(" 中国梦组委会副秘书长—王海渊")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font",staticStyle:{"text-align":"left","line-height":"0.3rem","width":"100%"}},[_vm._v(" 商业策略专家—蒋松林")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font"},[_vm._v("展望过去，国普惠民将在全国覆盖线下服务网点，丰富线上惠民服务，国普惠民是我们共同的生活品质保证工程，“全民参与、全民推荐、全民服务、全民监督，系统化严选，为百姓生活提供更放心、更贴心的服务！ “国普好家风，奉敬贤德人。”！国普积极与公益组织对接、合作，让家家有家训，户户好家风”。")]),_vm._v(" "),_c('span',{staticClass:"fivBoxNav_font",staticStyle:{"font-weight":"bold","text-indent":"0rem"}},[_vm._v("期待我们一起为惠民事业添砖加瓦，共献爱心！")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"xinxiBox_two"},[_c('div',{staticClass:"xinxiBox_twoBox"},[_c('div',{staticClass:"twoBoxOne"},[_c('img',{staticStyle:{"width":"39%","height":"56%"},attrs:{"src":require("../../assets/logozc.png"),"alt":""}})]),_vm._v(" "),_c('di